.App {
  text-align: center;
}

body {
  background-color: #282c34;
  color: white;
}

button {
  background-color: transparent;
  display: flex;
  border: 2px ridge rgba(129, 67, 14, 0.4);
  min-height: 20px;
  min-width: 20px;
  border-radius: 4px;
  background-color: rgb(30, 15, 0);
  color: aquamarine;
  justify-content: center;
  align-items: center;
}

main {
  margin-top: 120px;
  padding: 5px;

  overflow-y: hidden;

  &>section{
    min-height: 50vh;
  }
}