header{
    position: fixed;
    display: grid;
    top: 0;
    left: 0;
    width: 100%;

    justify-content: center;
    text-align: center;

    background-color: rgba(0, 37, 69, 0.8);
    box-shadow: 0 0 4px 6px rgb(0, 21, 39);

    filter: drop-shadow(0 0 2px black);

    &>abbr {
        display: inline-block;
        padding: 20px 0;

        letter-spacing: 4px;
        font-size: 30px;
    }

    z-index: 20;
}