ul,
li {
    list-style: none;
}

ul {
    * {
        position: relative;
    }
}

section {
    display: flex;
    flex-direction: column;

    align-items: center;
    margin: 5px;
    box-shadow: 0 0 2px black;

    padding: 20px 0;

    h1:first-child {
        margin: 20px 0;
    }
}

.index {
    display: flex;
    flex-wrap: wrap;

    min-height: 40vh;
    width: 100%;

    justify-content: center;
    align-items: center;


    overflow: hidden;

    li {
        align-items: center;
        margin: 5px 15px;

        &>button {
            width: 100%;
            height: 100%;
            min-width: 160px;
            min-height: 100px;




            border: none;
            box-shadow: 0 0 4px black;


            &>:first-child {
                position: absolute;
                top: 0;
                left: 0;
                right: 0;
                margin: auto;

                transform: scale(1);

                transition: letter-spacing 2s,
                    transform 3s,
                    box-shadow 2s;

                color: antiquewhite;
                filter: drop-shadow(0px 2px 4px black);
            }

        }

        &>button:hover {
            letter-spacing: 5px;
            transform: scale(1.1);
            animation: AnimationBkg 4s linear backwards infinite;
            box-shadow: 0 0 5px black;
        }
    }
}

@keyframes AnimationBkG {
    0% {
        background-position: 50% 50%;
    }

    50% {
        background-position: 100% 40%;
    }

    100% {
        background-position: 50% 50%;
    }
}



.rules {
    display: grid;
    grid-template-columns: 40% 40%;

    width: 100%;

    justify-content: space-around;

    article {
        border-radius: 4px;

        &>ul {
            width: auto;

            &>li {
                box-shadow: 0 0 4px black;

                margin: 10px 0;

                h2 {
                    letter-spacing: 1px;
                    margin: 12px 0;
                }
                h3 {
                    margin: 8px 0;
                }

                &>p {
                    letter-spacing: 2px;
                }

                div {
                    align-items: center;
                    margin: 4px 0;

                    &>ul {
                        width: 90%;
                    }
                }
            }
        }

    }
}

div.rules {
    button {
        min-width: 20px;
        min-height: 20px;
        font-size: 1.5rem;
    }

    .InMouseEdit--Div{
        position: absolute;
        display: flex;
        width: 60px;
        height: 20px;
        
        top: 0;
        right: 10px;
        
        padding: 4px 0;
        
        flex-direction: row;

        justify-content: space-around;
    }

    article.New {
        background-color: rgba(39, 39, 39, 0.4);
        border: 5px solid rgb(39, 39, 39);

        form {
            display: flex;
            flex-direction: column;

            input,
            textarea {
                background: transparent;
                color: aqua;
                padding: 5px 2px;
            }

            .description{
                textarea{
                    width: 80%;
                    
                }
            }

            .id {
                input {
                    width: 30px;
                }
            }
        }
    }

    article.Default {
        background-color: rgba(177, 150, 0, 0.4);
        border: 5px solid gold;
    }
}

/*cel*/
@media only screen and (max-width: 667px) {
    div.rules {
        display: flex;
        flex-direction: column;
    }
}